@use 'base/variables' as v;
@use 'base/mixins' as m;

.proceso-cafe {
  margin-bottom: 2rem;

  &:last-of-type {
    margin-bottom: 0;
  }

  .imagen,
  .texto {
    flex: 1;
  }

  @include m.tablet {
    display: flex;
    gap: v.$separacion;
    align-items: center;
  }

  &:nth-child(even) { //todos los pares
    .texto {
      order: 1;
    }
    .imagen {
      order: 2;
    }
  }

  .texto{
    margin-top: 1rem;
    h3 {
      margin: 0;
      text-align: left;
      color: v.$primario;
      &::after {
        content: '';
        display: block;
        width: 50%;
        height: 2px;
        background-color: v.$secundario;
      }
    }
  }

}

